import React from 'react'
import templateBg from '../../assets/images/ProductImage/TemplateBg.png'
import template1 from '../../assets/images/ProductImage/Template1.png'


const Template = () => {
  return (
    <div className='template-product'>
        <img src={templateBg} className='template-bg'/>
        <div className='template-container'>
            <div className='each-template'>
            <div className='image-temblate'>
                  <img src={template1} alt='template'/>
                </div>
                <div className='template-title'>
                    Template 1
                </div>
            </div>
            <div className='each-template'>
            <div className='image-temblate'>
                  <img src={template1} alt='template'/>
                </div>
                <div className='template-title'>
                    Template 2
                </div>
            </div>
            <div className='each-template'>
            <div className='image-temblate'>
                  <img src={template1} alt='template'/>
                </div>
                <div className='template-title'>
                    Template 3
                </div>
            </div>
            <div className='each-template'>
            <div className='image-temblate'>
                  <img src={template1} alt='template'/>
                </div>
                <div className='template-title'>
                    Template 4
                </div>
            </div>
            <div className='each-template'>
            <div className='image-temblate'>
                  <img src={template1} alt='template'/>
                </div>
                <div className='template-title'>
                    Template 5
                </div>
            </div>
        </div>
    </div>
  )
}

export default Template