import React from 'react'
import ProductBg from '../../assets/images/ProductImage/ProductBg.png';
import ProductBgMobile from '../../assets/images/MobileImages/HeroBg.png';



const Product = () => {
  return (
    <div className='product-ourproduct'>
    <img className='our-product-bg' src={ProductBg}/>
    <img className='our-product-bg-m' src={ProductBgMobile}/>
    <div className='product-ourproduct-container'>
        <h1>TAKE A LOOK ON OUR PRODUCT</h1>
        <h2>Where You Can Find High Quality</h2>
        <h3>Designs|Websites|Mobile Apps...</h3>
       
 </div>
        
        </div>
  )
}

export default Product